<template>

	<div>
		<nav class="col-sm-3 col-md-2 hidden-xs-down bg-faded sidebar">
	  		<ul class="nav nav-pills flex-column">
	    		<li class="nav-item">
	      			<a class="nav-link active" href="#">Overview <span class="sr-only">(current)</span></a>
	    		</li>
	    		<li class="nav-item">
	      			<a class="nav-link" href="#">Reports</a>
	    		</li>
	  		</ul>
		</nav>
	</div>
	
</template>

<script>

export default {
  
  computed: {
  },

  data () {
    name: 'sidebar'
    return {
    }
  },
}
</script>

<style type="text/css" media="screen">
	/*
	 * Sidebar
	 */

	.sidebar {
	  position: fixed;
	  top: 51px;
	  bottom: 0;
	  left: 0;
	  z-index: 1000;
	  padding: 20px;
	  overflow-x: hidden;
	  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
	  border-right: 1px solid #eee;
	}

	/* Sidebar navigation */
	.sidebar {
	  padding-left: 0;
	  padding-right: 0;
	}

	.sidebar .nav {
	  margin-bottom: 20px;
	}

	.sidebar .nav-item {
	  width: 100%;
	}

	.sidebar .nav-item + .nav-item {
	  margin-left: 0;
	}

	.sidebar .nav-link {
	  border-radius: 0;
	}	
</style>


