<template>
  <div id="app">
    <notifications />
    <div v-if="$auth.ready() && loaded">
      <header-menu v-if="auth"></header-menu>
      <div class="container-fluid">
        <router-view></router-view>
      </div>
    </div>  

    <div v-if="!$auth.ready() || !loaded">
      <div style="text-align:center; padding-top:50px;">
        Loading site...
      </div>
    </div>

  </div>
  
</template>

<script>

import headerMenu from './components/Header';
import sideBar from './components/Sidebar';

export default {
  name: 'app',
  components: {
    headerMenu,
    sideBar
  },
  data() {
    return {
      context: 'app context',
      loaded: false
    };
  },
  mounted() {
    var _this = this;
    // Set up $auth.ready with other arbitrary loaders (ex: language file).
    setTimeout(function () {
        _this.loaded = true;
    }, 500);
  },
  created() {
    var _this = this;
    this.$auth.ready(function () {
        console.log('ready ' + this.context);
    });
    // Vue.http.interceptors.push(function (req, next) {
    //     next(function (res) {
    //         if ( ! res.ok) {
    //             _this.$router.push({name: 'error-502'})
    //         }
    //     });
    // });
  },
  methods: {
    logout() {
        this.$auth.logout({
          makeRequest: true,
          success() {
              console.log('success ' + this.context);
          },
          error() {
              console.log('error ' + this.context);
          }
        });
    },
    unimpersonate() {
      this.$auth.unimpersonate({
        success() {
            console.log('success ' + this.context);
        },
        error() {
            console.log('error ' + this.context);
        }
      });
    }
  }
}
</script>




