<template>
  
  <b-form @submit="onSubmit" class="form-signin">
  	<h2 class="form-signin-heading">Inicie sesión</h2>
      <b-form-group id="username"
                    label-for="UserName">
        <b-form-input id="UserName"
                      type="email" v-model="form.email" required
                      placeholder="Ingrese su nombre de usuario">
        </b-form-input>
      </b-form-group>

      <b-form-group id="password"
                    label-for="PassWord">
        <b-form-input id="PassWord"
                      type="password" v-model="form.password" required
                      placeholder="Ingrese su contraseña">
        </b-form-input>
      </b-form-group>

      <b-button type="submit" class="btn-lg btn-block" variant="primary">Submit</b-button>
  
  </b-form>

</template>

<script>

  
  
import {requestLogin}  from '../endpoints';
//import Errors from '../common/js/Errors';

export default {


  computed: {
  },

  data () {
  	name: 'login'
    return {
      isSubmitting: false,
      form: {
        email: '',
        password: ''
      }
      //errors: new Errors(),
    }
  },
  methods: {
    onSubmit(evt) {
      this.isSubmitting = true;
      
      this.$auth.login({
        data: this.form,
        rememberMe: true,
        success() {
          this.isSubmitting = false;
          this.$router.push({ path: '/' });
        },
        error(error) {
          //this.errors.record(error.response.data);
          this.isSubmitting = false;
        }
      });
    }
  }
}
</script>

<style scoped>

body {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #eee;
}

.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

</style>


