import Vue from 'vue'
import VeeValidate from 'vee-validate'
import Router from 'vue-router'
import Home from '@/components/Home'
import NotFound from '@/components/404'
import Login from '@/components/Login'
import TypeUser from '@/components/TypeUser'


Vue.use(VeeValidate)
Vue.use(Router)

export default new Router({
  routes: [
    {
  		path: '/login',
	  	name: 'Login',
	  	hidden: true,
	  	component: Login,
	  	meta: {auth: false},
    },
    {
  		path: '/',
	  	name: 'Home',
	  	component: Home,
	  	meta: {auth: true},
    },
    {
  		path: '/typeuser',
	  	name: 'TypeUser',
	  	component: TypeUser,
	  	meta: {auth: true},
    },
    {
		path: '/404',
	    component: NotFound,
	    name: '',
	    hidden: true
    },
    {
	    path: '*',
	    hidden: true,
	    redirect: { path: '/404' }
    }
  ]
})



